<script type="text/ecmascript-6">
import { API_URL, SETTING } from '@/assets/config'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'UserComponent',
  data () {
    return {
      api_url: API_URL,
      setting: SETTING,
      Module: {},
      user: Cache.getUser(),
      permissions: Cache.getUser('permission'),
      isSuper: false,  // 是否超级用户
      avatar: null,
      pageSet: {}
    }
  },
  methods: {
    checkPermissions () {
      // 内容管理
      const contentManagement = ['content_management']
      this.checkModule('contentManagement', contentManagement)
      // 用户组管理
      const role = ['post_role', 'update_role', 'delete_role']
      this.checkModule('role', role)
      // 用户管理
      const user = ['post_user', 'update_user', 'delete_user']
      this.checkModule('user', user)
      // 公告管理
      const notice = ['update_notification', 'delete_notification', 'post_notification', 'top_notification']
      this.checkModule('notice', notice)
      // 自定义字段
      const customForm = ['add_field', 'update_field', 'drop_field']
      this.checkModule('customForm', customForm)
      // 数据库备份
      const backUp = ['back_up']
      this.checkModule('backUp', backUp)


      // 管理模块
      const basicInformation = ['basic_information']
      this.checkModule('basicInformation', basicInformation)
      const academicAchievements = ['academic_achievements']
      this.checkModule('academicAchievements', academicAchievements)
      const talentDevelopment = ['talent_development']
      this.checkModule('talentDevelopment', talentDevelopment)
      const score = ['score']
      this.checkModule('score', score)
      const managementWork = ['management_work']
      this.checkModule('managementWork', managementWork)
    },
    checkModule (key, permissions) {
      if (this.isSuper) {
        this.Module[key] = true
        return
      }
      for (const permission of permissions) {
        if ($.inArray(permission, this.permissions) > -1) {
          this.Module[key] = true
          break
        }
      }
    }
  },
  mounted () {
    $('body').append('<script src="static/js/contabs.js"><\/script>')
    $('body').append('<script src="static/js/hplus.js?v=4.1.0"><\/script>')
  },
  created () {
    if (!Cache.getToken || !Cache.getUser()) {
      this.$router.push('/login')
    } else {
      Service.PageSet.get().then(response => {
        this.pageSet = response.data
        $('title.projectTitle').text(this.pageSet.web_name)
      })
      if ($.inArray('admin', this.permissions) > -1) {
        this.isSuper = true
      }
      this.checkPermissions()
    }
  }
}
</script>

